.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
}

.taskField {
    display: flex;
    flex-direction: column;
    width: 80%;
}
.button {
    width: 80%;
}

.button:hover {
    cursor: pointer;
}

.taskField textarea {
    font-family: "Roboto Condensed";
    font-weight: bolder;
}

.close {
    margin-left: auto;
    width: 10%;
    border: 1px solid #939393;
    border-radius: 3px;
}

.close:hover {
    background-color: #939393;
    color: white;
}

@media screen and (max-width: 767px) {
    .close {
        width: 20%;
    }
}
